#App {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.6rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(
    to left,
    rgba(7, 27, 82, 1) 0%,
    rgba(0, 128, 128, 1) 100%
  );
}

#App .container {
  width: 80%;
  min-width: 60rem;
  max-width: 100rem;
  height: 100%;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
}

button[class*="button"] {
  padding: 10px 12px;
  border-radius: 15px;
  border: none;
  font-size: 12px;
  min-width: 75px;
  max-width: 200px;

  text-align: center;
  cursor: pointer;
  box-shadow: 3px 2px 0 0 #3273a89f;
  color: white;
  background: rgba(0, 0, 0, 0.575);
  transition: transform 0.1s ease;
}

button[class*="button"]:disabled {
  padding: 10px 12px;
  border-radius: 15px;
  border: none;
  font-size: 12px;
  min-width: 75px;
  text-align: center;
  cursor: pointer;
  color: white;
  background: rgba(0, 0, 0, 0.205);
  pointer-events: none;
}

button[class*="button"]::after,
button[class*="button"]::before {
  content: "";
  position: absolute;
  opacity: 0.2;
  background-color: rgb(0, 0, 0);
  border-radius: inherit;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  z-index: -1;
  transition: transform 0.1s ease;
}

button[class*="button"]:hover {
  transform: translate(-3px, -3px);
}

button[class*="button"]:hover::after {
  transform: translate(-1.5px, -0.1.5px);
}

button[class*="button"]:hover::before {
  transform: translate(3px, 3px);
}

input[class*="inputTxt"] {
  font-size: 12px;
  border: none;
  appearance: none;
  background: #f2f2f2b9;
  padding: 10px;
  outline: none;
  width: 250px;
  border-radius: 5px;
}

span[class*="instruction"] {
  color: white;
  margin: 5px;
  padding-top: 10px;
}

